"use client"

import { useSession } from "next-auth/react"
import { useEffect, useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Switch } from "@/components/ui/switch"
import Link from "next/link"

interface Application {
  id: string
  appliedAt: string
  status: string
  job: {
    id: string
    title: string
    location: string
    salary?: string
    company: {
      name: string
    }
  }
}

export default function ProfilePage() {
  const { data: session, status } = useSession()
  const [applications, setApplications] = useState<Application[]>([])
  const [isEmployer, setIsEmployer] = useState(false)
  const [loading, setLoading] = useState(true)
  const [resumeFile, setResumeFile] = useState<File | null>(null)
  const [uploading, setUploading] = useState(false)

  useEffect(() => {
    if (session?.user) {
      setIsEmployer(session.user.isEmployer || false)
      fetchApplications()
    }
  }, [session])

  const fetchApplications = async () => {
    try {
      const response = await fetch('/api/applications')
      if (response.ok) {
        const data = await response.json()
        setApplications(data.applications)
      }
    } catch (error) {
      console.error('Error fetching applications:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleResumeUpload = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!resumeFile) return

    setUploading(true)
    const formData = new FormData()
    formData.append('resume', resumeFile)

    try {
      const response = await fetch('/api/upload-resume', {
        method: 'POST',
        body: formData,
      })

      if (response.ok) {
        alert('Resume uploaded successfully!')
        setResumeFile(null)
      } else {
        alert('Failed to upload resume')
      }
    } catch (error) {
      console.error('Error uploading resume:', error)
      alert('Failed to upload resume')
    } finally {
      setUploading(false)
    }
  }

  const updateEmployerStatus = async (newStatus: boolean) => {
    try {
      const response = await fetch('/api/user/employer-status', {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ isEmployer: newStatus }),
      })

      if (response.ok) {
        setIsEmployer(newStatus)
        // Refresh session to update navbar
        window.location.reload()
      }
    } catch (error) {
      console.error('Error updating employer status:', error)
    }
  }

  if (status === "loading" || loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-lg">Loading...</div>
      </div>
    )
  }

  if (!session) {
    return (
      <div className="text-center py-12">
        <h1 className="text-2xl font-bold mb-4">Please sign in to view your profile</h1>
        <Button onClick={() => window.location.href = '/api/auth/signin'}>
          Sign In
        </Button>
      </div>
    )
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold">Profile</h1>
      </div>

      {/* User Info */}
      <Card>
        <CardHeader>
          <CardTitle>Account Information</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label>Name</Label>
              <p className="text-lg">{session.user?.name}</p>
            </div>
            <div>
              <Label>Email</Label>
              <p className="text-lg">{session.user?.email}</p>
            </div>
          </div>
          
          <div className="flex items-center space-x-2">
            <Switch
              id="employer-mode"
              checked={isEmployer}
              onCheckedChange={updateEmployerStatus}
            />
            <Label htmlFor="employer-mode">
              I am an employer (can post jobs)
            </Label>
          </div>
        </CardContent>
      </Card>

      {/* Resume Upload */}
      {!isEmployer && (
        <Card>
          <CardHeader>
            <CardTitle>Resume</CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleResumeUpload} className="space-y-4">
              <div>
                <Label htmlFor="resume">Upload Resume (PDF, DOC, DOCX)</Label>
                <Input
                  id="resume"
                  type="file"
                  accept=".pdf,.doc,.docx"
                  onChange={(e) => setResumeFile(e.target.files?.[0] || null)}
                />
              </div>
              <Button type="submit" disabled={!resumeFile || uploading}>
                {uploading ? 'Uploading...' : 'Upload Resume'}
              </Button>
            </form>
          </CardContent>
        </Card>
      )}

      {/* Applications */}
      {!isEmployer && (
        <Card>
          <CardHeader>
            <CardTitle>My Applications ({applications.length})</CardTitle>
          </CardHeader>
          <CardContent>
            {applications.length === 0 ? (
              <div className="text-center py-8 text-gray-500">
                <p>You haven't applied to any jobs yet.</p>
                <Link href="/" className="text-blue-600 hover:text-blue-800 underline">
                  Browse Jobs
                </Link>
              </div>
            ) : (
              <div className="space-y-4">
                {applications.map((application) => (
                  <div key={application.id} className="border rounded-lg p-4">
                    <div className="flex items-start justify-between">
                      <div className="space-y-1">
                        <Link 
                          href={`/jobs/${application.job.id}`}
                          className="text-lg font-semibold text-blue-600 hover:text-blue-800"
                        >
                          {application.job.title}
                        </Link>
                        <p className="text-gray-600">{application.job.company.name}</p>
                        <p className="text-sm text-gray-500">{application.job.location}</p>
                      </div>
                      <div className="text-right space-y-1">
                        <Badge variant={
                          application.status === 'Applied' ? 'default' :
                          application.status === 'Reviewed' ? 'secondary' :
                          application.status === 'Accepted' ? 'default' : 'destructive'
                        }>
                          {application.status}
                        </Badge>
                        <p className="text-sm text-gray-500">
                          Applied {formatDate(application.appliedAt)}
                        </p>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      )}

      {/* Employer Actions */}
      {isEmployer && (
        <Card>
          <CardHeader>
            <CardTitle>Employer Actions</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <Link href="/post-job">
                <Button className="w-full">Post New Job</Button>
              </Link>
              <Link href="/my-jobs">
                <Button variant="outline" className="w-full">Manage My Jobs</Button>
              </Link>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
